.c-landing {
  &__section {
    padding: 40px 5%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    &--full-height {
      height: 100vh; }
    &--light-blue {
      background: linear-gradient(92deg, #e1edfd, transparent); } }
  &__two-sides {
    display: grid;
    grid-template-columns: auto 500px;
    grid-gap: 40px; }
  &__image {
    width: 100%; }
  &__container {
    max-width: 1200px;
    margin: 0 auto; }
  &__title {
    font-size: 42px; }
  &__subtitle {
    margin-top: 24px;
    font-size: 28px; } }
